@import "../assets/styles/index";


.comment {
  margin-left: 20%;
  margin-right: 20%;

  &__header {
    font-size: 16px;
    line-height: 18px;
    color: #666;
    text-transform: uppercase;
    border-bottom: 3px solid #666;
    padding-bottom: 10px;
    font-family: "sans-condense_bold";
    margin-bottom: 10px;
  }
  &__info {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  &-item {
    width: 100%;
    padding-bottom: 5px;
    padding-top: 7px;
    display: flex;

    border-bottom: 1px solid #e6e6e6;
  }
  &-item1 {
    margin-left: 5%;
    width: 95%;
    padding-bottom: 5px;
    padding-top: 7px;
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid #e6e6e6;
  }
  &__img {
    height: 50px;
    width: auto;
    margin-right: 20px;
  }
  &__name {
    font-weight: bold;
    font-size: 12px;
    color: #666;
  }
  &__description {
    padding: 1px 0;
    font-size: 12px;
    line-height: 16px;
    padding-top: 4px;

    color: rgb(26, 26, 26);
  }
}

